.Nav {
    display: flex;
    margin-bottom: 25px;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    border-top: solid #39ff14 1px;
    border-bottom: solid #39ff14 1px;
    padding: 5px 0px;
  }
  
  .Random {
    position: relative;
    left: 0px;
    border-radius: 5px;
    border: solid #39ff14 1px;
    background: rgb(44, 46, 49);
    color: #39ff14;
    font-size: 15px;
    max-width: 200px;
    min-width: 200px;
    max-height: 60px;
    min-height: 60px;
  }
  
  .Random:hover {
    box-shadow: inset 0 0 10px #39ff14;
    font-size: 20px;
  }