.input {
    padding: 5px;
    margin-left: 450px;
    margin-top: 15px;
    justify-content: space-evenly;
    width: 200px;
    align-items: center;
    background-color: azure;
    border-radius: 10px;
    color: #1a9404;
    font-weight: bold;
}

.boton {
    padding: 5px;
