    align-items: center;
    background-color:  rgb(44, 46, 49); ;
    border-radius: 5px;
    color: #39ff14;
    border: 1px solid #39ff14;
} 

.boton:hover {
    box-shadow: inset 0 0 10px #39ff14;
    font-size: 20px;
  }